@media (max-width: 1140px) {
  .card {
    width: calc(50% - 15px);
  }

  .footer {
    padding-top: 40px;
    padding-bottom: 60px;
  }

  .footer-row {
    flex-direction: column-reverse;
    align-items: center;
    text-align: center;
  }

  .footer-col {
    margin-bottom: 50px;
  }

  .footer-col:first-child {
    margin-bottom: 0;
  }
}

@media (max-width: 760px) {
  .header__title {
    font-size: 32px;
  }

  .header__subtitle {
    font-size: 14px;
  }

  .portfolio {
    padding-top: 40px;
    padding-bottom: 60px;
  }

  .portfolio__header {
    font-size: 32px;
    margin-bottom: 30px;
  }

  .portfolio__cards-wrapper {
    justify-content: center;
  }

  .skills {
    padding-top: 40px;
    padding-bottom: 60px;
  }

  .skills__header {
    font-size: 32px;
    margin: 0 0 30px;
  }

  .skills__info {
    row-gap: 20px;
  }

  .skills__info span {
    font-size: 26px;
  }

  .skills__info p {
    font-size: 14px;
  }

  .card {
    max-width: 530px;
    width: 100%;
  }

  .card p {
    font-size: 14px;
  }

  .card__link {
    padding: 15px;
  }

  .card__link-img {
    margin-bottom: 10px;
  }

  .card__link-title {
    font-size: 18px;
    margin-bottom: 10px;
  }

  .contacts {
    padding-top: 40px;
    padding-bottom: 60px;
  }

  .contacts__header {
    font-size: 32px;
    margin: 0 0 30px;
  }

  .contacts__info {
    row-gap: 20px;
  }

  .contacts__info span {
    font-size: 26px;
  }

  .contacts__info p {
    font-size: 14px;
  }

  .footer__copyright-name {
    font-size: 18px;
    margin-bottom: 5px;
  }

  .footer__copyright-name p {
    font-size: 14px;
  }

  .footer__icons p {
    font-size: 14px;
  }

  .footer__contacts p {
    font-size: 14px;
    margin: 0;
    margin-top: 15px;
  }
}

@media (max-width: 500px) {
  .nav__row {
    justify-content: space-between;
  }

  .nav__dark-mode {
    order: 0;
  }
}
