.nav {
  font-family: "Poppins", sans-serif;
  padding: 20px 0;
  background-color: var(--accent);
  color: var(--white);
}

.nav__row {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  column-gap: 30px;
  row-gap: 20px;
  flex-wrap: wrap;
}

.nav__logo {
  margin-right: auto;
  font-size: 24px;
  font-weight: 700;
  transition: color 0.2s ease-in;
}

.nav__logo:hover {
  color: var(--light-blue);
}

.nav__list {
  display: flex;
  align-items: center;
  column-gap: 40px;
}

.nav__list-link {
  transition: color 0.2s ease-in;
}

.nav__list-link:hover {
  color: var(--light-blue);
}

.nav__list-link--active {
  position: relative;
}

.nav__list-link--active::before {
  content: "";
  position: absolute;
  left: 0;
  top: 100%;
  display: block;
  width: 100%;
  height: 2px;
  background-color: var(--light-blue);
}
