.card {
  max-width: 540px;
  background-color: var(--project-card-bg);
  box-shadow: 0px 10px 40px var(--box);
  transition: transform 0.2s ease-in;
}

.card:hover {
  transform: translateY(-15px);
}

.card p {
  font-size: 16px;
  line-height: 130%;
}

.card__link {
  padding: 20px;
  display: block;
  color: var(--project-card-text);
  transition: color 0.2s ease-in;
}

.card__link:hover {
  color: var(--hover);
}

.card__link-img {
  margin-bottom: 20px;
}

.card__link-title {
  margin-bottom: 13px;
  font-weight: 600;
  font-size: 24px;
  line-height: 1.3;
}

.card__frontend {
  display: flex;
  flex-direction: column;
}

.card__frontend span {
  font-size: 25px;
  text-align: center;
  margin-bottom: 10px;
}

.card__frontend p {
  font-size: 16px;
  text-align: center;
}

.card__backend {
  display: flex;
  flex-direction: column;
}

.card__backend img {
  width: 78%;
}

.card__backend span {
  font-size: 25px;
  text-align: center;
  margin-bottom: 10px;
}

.card__backend p {
  font-size: 16px;
  text-align: center;
}
