.footer {
  padding-top: 45px;
  padding-bottom: 129px;
  background-color: var(--accent);
  color: var(--white);
}

.footer-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.footer-col {
  max-width: 350px;
  flex-grow: 1;
}

.footer__copyright {
  color: var(--light-blue);
  line-height: 1.3;
}

.footer__copyright-name {
  font-weight: 700;
  font-size: 28px;
  margin-bottom: 15px;
}

.footer__copyright-name p {
  font-size: 16px;
  font-weight: 600;
}

.footer__icons p {
  font-weight: 600;
  font-size: 16px;
  line-height: 1.3;
}

.footer__icons-row {
  display: flex;
  align-items: center;
}

.footer__icons-row > * + * {
  margin-left: 30px;
}

.footer__socials-icons {
  margin-top: 15px;
  fill: var(--white);
  transition: fill 0.2s ease-in;
}

.footer__socials-icons:hover {
  fill: var(--light-blue);
}

.footer__contacts p {
  margin: 15px 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.3;
  color: var(--light-blue);
}

.footer__button {
  display: inline-block;
  height: 50px;
  padding-left: 30px;
  padding-right: 30px;
  border: 3px solid var(--white-border);
  border-radius: 50px;
  color: var(--white);
  line-height: 44px;
  font-weight: 700;
  font-size: 16px;
  text-transform: uppercase;
  text-decoration: none;
  transition: all 0.2s ease-in;
}

.footer__button:hover {
  background-color: var(--bg-color);
  color: var(--dark-blue);
}
