.contacts {
  padding-top: 80px;
  padding-bottom: 115px;
}

.contacts__header {
  margin: 0 0 70px;
  font-weight: 700;
  font-size: 46px;
  text-align: center;
  color: var(--accent);
}

.contacts__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  row-gap: 40px;
}

.contacts__info span {
  font-size: 40px;
  line-height: 1.3;
}

.contacts__info p {
  font-size: 18px;
  line-height: 1.5;
  margin-top: 20px;
}

.contacts__info a {
  color: var(--light-blue);
}
