.header {
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  background-image: url("./../../img/cover.jpg");
}

.header__title {
  margin-bottom: 25px;
  font-size: 56px;
  text-align: center;
  color: var(--white);
}

.header__subtitle {
  font-size: 16px;
  line-height: 1.5;
  text-align: center;
  letter-spacing: 0.2em;
  text-transform: uppercase;
  color: var(--white);
}

.header__arrow {
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translate(-50%, 0);
  width: 48px;
  height: 48px;
}
