.btn__dark-mode {
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 51px;
  height: 26px;
  padding: 5px;
  border-radius: 50px;
  background-color: #272727;
  order: 1;
}

.btn__dark-mode::before {
  content: "";
  position: absolute;
  top: 1px;
  left: 1px;
  display: block;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background-color: var(--white);
  transition: left 0.2s ease-in;
}

.btn__dark-mode--active::before {
  left: 26px;
}

.btn__dark-mode-icons {
  position: relative;
  z-index: 9;
}
