*,
*::after,
*::before {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

:root {
  --black: #171718;
  --black-gray: #252526;
  --black-border: #171718;
  --white: #fff;
  --white-border: #fff;
  --dark-blue: #1e4776;
  --blue: #275bec;
  --blue-gray: #7e9bbd59;
  --light-blue: #7e9ec9;
  --bg-color: var(--white);
  --hover: var(--blue);
  --box: var(--blue-gray);
  --accent: var(--dark-blue);
  --text-color: var(--black);
}

html,
body {
  margin: 0;
  scroll-behavior: smooth;
  font-family: "Montserrat", sans-serif;
  color: var(--text-color);
}

.dark {
  background-color: #252526;
  --text-color: var(--white);
  --project-card-bg: var(--black-gray);
  --project-card-text: var(--white);
  --title-1: var(--black);
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: var(--white);
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  padding: 0;
  border: none;
  background: none;
  cursor: pointer;
}

.container {
  max-width: 1140px;
  padding: 0 15px;
  margin: 0 auto;
}
